interface Iqa {
  q: string;
  a: string;
}

import { Component, OnInit, AfterContentInit } from "@angular/core";
import * as Parallax from "parallax-js";
import { ActivatedRoute } from "@angular/router";

@Component({
  selector: "app-home",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.css"]
})
export class HomeComponent implements OnInit, AfterContentInit {
  constructor(private route: ActivatedRoute) {}

  qa: Iqa[] = [
    {
      q: "How much should I spend?",
      a:
        "We charge Rs. 250 only for you to build the future, get experience and have lots of fun."
    },
    {
      q: "How many people can form a team?",
      a: "Some say 2 is a crowd. We say go up to 4."
    },
    {
      q: "What can I win?",
      a:
        "There are many exciting prizes for you to show off to your friends. Also get cash prize up to 10k with which you can eat in iFoods for the whole month."
    },
    {
      q: "What about equipment?",
      a:
        "Sorry, but we can not provide equipment for 250 bucks unless it is in dollars. So, you have to bring your own materials to evolve them into new products."
    },
    {
      q: "What will be the duration of hackathon?",

      a: "It starts at 9am. We’ll leave the math to you."
    },
    {
      q: "Will we get food during the event?",
      a:
        "They say empty vessels make the most noise. We would like to avoid the same."
    },
    {
      q: "Projects will be evaluated on what basis?",
      a: "The rubrics will be announced on the day of hackathon."
    },
    {
      q: "Will we get attendance?",
      a:
        "On-Duty will be provided for all the participants for 12th and 13th December."
    },
    {
      q: "Will we get any certificates?",
      a: "We don’t plan on sending any of you back empty handed."
    },
    {
      q: "Who all can participate in this hackathon?",
      a: "Hopefully you all our dear students."
    },
    {
      q: "Can the registration fee be refunded?",
      a: "Unfortunately, that would be a one-way road."
    }
  ];
  fragment: string;
  ngOnInit() {
    this.route.fragment.subscribe(frag => {
      this.fragment = frag;
    });
  }

  ngAfterContentInit() {
    const logo = document.getElementById("logo");
    const parallaxInstance = new Parallax(logo, {
      hoverOnly: false,
      relativeInput: true
    });
    parallaxInstance.friction(0.1, 0.2);

    const scene = document.getElementById("scene");
    const parallaxScene = new Parallax(scene, {
      hoverOnly: false,
      relativeInput: false
    });
    try {
      if (this.fragment) {
        document.getElementById("#" + this.fragment).scrollIntoView();
        this.fragment = null;
      }
    } catch (e) {}
  }
}
